<template>
  <div class="indexroot">
    <Navigation :browserWidth="this.fixedBrowserWidth"></Navigation>
    <Header :browserWidth="this.fixedBrowserWidth"></Header>
    <First :browserWidth="this.fixedBrowserWidth"></First>
    <Seckill></Seckill>
    <Spec></Spec>
    <div ref="$ChannelSkeleton"
      :style="channelSkeleton">
    </div>
      <Channel ref="$Channel" :style="channelStyle"></Channel>


    <div ref="$box"></div>
    <Feeds ref="Feeds"
      :style="FeedsStyle"></Feeds>
    <Footer :browserWidth="this.fixedBrowserWidth"></Footer>
    <div>1</div>
  </div>
</template>

<script>
// @ is an alias to /src
import Navigation from '@/components/Navigation.vue'
import Header from '@/components/Header.vue'
import First from '@/components/First.vue'
import Footer from '@/components/Footer.vue'
import Seckill from '@/components/Seckill.vue'
import Spec from '@/components/Spec.vue'
// import Channel from '@/components/Channel.vue'
// import Feeds from '@/components/Feeds.vue'

export default {
  name: 'home',
  components: {
    Navigation,
    Header,
    First,
    Footer,
    Seckill,
    Spec,
    Channel: resolve => require(['@/components/Channel.vue'], resolve),
    Feeds: resolve => require(['@/components/Feeds.vue'], resolve),
    // Channel,
    // Feeds
  },
  data(){
    return{
      browserWidth:1330,//浏览器宽度
      fixedBrowserWidth:0,//性能优化
      channelSkeleton:{
        width:'1190px',
        height:'200px'
      },
      channelStyle:{
        display:'none' ,
      },
      FeedsStyle:{
        display:'none'
      }
    }
  },
  computed: {
    setmainwidth() {
      return {
        '--browserWidth': this.browserWidth,
      }
    },
  },
  watch: {
    browserWidth(val) {
      if(!this.timer) {
      // this.browserwidth=val
      this.timer = true
      let _this = this
        setTimeout(function () {
          _this.getbrowserwidth();
          _this.timer = false
        }, 500)
      }
    },//获取浏览器宽度
  },

  methods:{
    getbrowserwidth(){
      this.fixedBrowserWidth=Number(this.browserWidth);
    },
    scrollIndex() {
      const { $box, } = this.$refs
      const { $ChannelSkeleton,$Channel,Feeds} = this.$refs
      const { channelSkeleton,channelStyle,boxStyle } = this
      const offset = $ChannelSkeleton.getBoundingClientRect();
      const offsetTop = offset.top;
      const offsetBottom = offset.bottom;
      const offsetHeight = offset.height;
      // 进入可视区域
      // console.log(offsetTop,offsetBottom)
      console.log( $ChannelSkeleton.getBoundingClientRect())
      // console.log( window.innerHeight)
      if (offsetTop <= window.innerHeight && offsetBottom >= 0) {
        // console.log('进入可视区域');
        channelStyle.display='block'
        // channelSkeleton.display='none'
      } else {
        // console.log('移出可视区域');
 
      }
    }

  },

  created() {
    this.fixedBrowserWidth=document.documentElement.clientWidth;
  },
  mounted() {
    const _this = this;
    window.onresize = () => {
      return (() => {
          _this.browserWidth= `${document.documentElement.clientWidth}`;
      })();
    };
    window.addEventListener("scroll", this.scrollIndex, true);
  },
  destroyed(){
    window.onresize = null;
    window.removeEventListener('scroll', this.scrollHandle, true);
  }
}
</script>

<style lang="less">
*{
    margin: 0;
    padding: 0;
}
a{
  text-decoration: none;
  font: 12px/1.5 Microsoft YaHei,Heiti SC,tahoma,arial,Hiragino Sans GB,"\5B8B\4F53",sans-serif;
  color: #666;
  cursor:pointer; 
}
li{
  list-style: none;
}
button{
  cursor:pointer;
}
.indexroot{
  background-color: #f4f4f4;
}

</style>